<template>
<el-row :gutter="20">
  <el-col :span="14"><div class="grid-content bg-purple">
        <!-- <div class="dashboard-stats" style="float: left; width: 48%;">
                      <div class="dashboard-stats-item" style=" height:120px;  background-color: #578ebe;text-align: center;padding-left:0;"> 
                                <h4 style="padding:0;"><a>所有用户余额</a></h4>
                                <p>每月10号可申请上月奖金</p>
                            </div>
                        </div> --> 
  <div style="height: 100%; border: 1px solid #e6e6e6; overflow: hidden; ">
                        <div class="dashboard-stats" style="float: left; width: 48%;background-color: #578ebe; margin:10px 0;">
                            <div class="dashboard-stats-item" style="text-align: center;padding-left:0;"> 
                                <h4 style="padding:0;"><a>所有用户余额</a></h4>
                                <button style='border:0; padding:0; background-color: #578ebe;'>去提现</button>
                                <p style='color:#fff;padding:0;  margin:3px 0; font-size:12px;'>每月10号可申请上月奖金</p>
                            </div>
                        </div>
                        <div class="dashboard-stats" style="float: right; width: 48%; background-color: #e35b5a;  margin:10px 0;">
                            <div class="dashboard-stats-item" style="text-align: center;padding-left:0;">  
                                <h4 style="padding:0">当然所有收益</h4> 
                                <p style='color:#fff;  font-size:12px;'>查看搜索</p>
                            </div>
                        </div> 
                        <div class="dashboard-stats" style="float: left; width: 48%; background-color: #578ebe;  margin:10px 0;">
                                <div class="dashboard-stats-item" style="text-align: center;padding-left:0;"> 
                                    <h4 style="padding:0">管理客户数</h4>
                                    <p style=" font-size:12px;">0</p>
                                </div>
                            </div>
                            <div class="dashboard-stats" style="float: right; width: 48%; background-color: #e35b5a;  margin:10px 0;">
                                <div class="dashboard-stats-item" style="text-align: center;padding-left:0;"> 
                                    <h4 style="padding:0">分站数</h4> 
                                    <p  style=" font-size:12px;">62</p>
                                </div>
                            </div> 
                    </div>

                    <section class="chart-container">
                            <el-row>      
                                 <el-row :gutter="20">
                                        <el-col :span="8"><div class="grid-content bg-purple">
                                            <div id="chartColumn" style="width:100%; height:300px;"></div> 
                                            </div></el-col>
                                    <el-col :span="8"><div class="grid-content bg-purple">
                                        
                                        <div id="chartBar" style="width:100%; height:300px;"></div>
                                        </div></el-col> 
                                        <el-col :span="8"><div class="grid-content bg-purple">
                                            
                                            <div id="chartPie" style="width:100%; height:300px;"></div>  
                                            
                                            </div></el-col> 
                                    </el-row>
                                    
                            
                            </el-row>
                        </section> 
      <el-row>  
</el-row>
      
      
      
      </div></el-col>
                <el-col :span="10"> 
                    <div class="grid-content bg-purple"> 
                        <!-- 右侧 -->
                        <div id="chartLine" style="width:100%; height:600px;"></div>
                    </div>
                </el-col> 
                <el-row>
                      <el-col :span="24"><div class="grid-content bg-purple-dark">
                          
                          <div id="chartLine1" style="width:100%; height:400px;"></div>
                          
                          </div></el-col>
                          <el-col :span="24"><div class="grid-content bg-purple-dark">
                          
                          <div id="chartLine2" style="width:100%; height:400px;"></div>
                          
                          </div></el-col>
                </el-row>
</el-row> 
 
</template>

<script>
    import echarts from 'echarts' 
    export default {
        data() {
            return {
                chartColumn: null,
                chartBar: null,
                chartLine: null,
                chartPie: null,
                chartLine1:null,
                chartLine2:null
            }
        },

        methods: {
            drawColumnChart() {
                this.chartColumn = echarts.init(document.getElementById('chartColumn'));
                this.chartColumn.setOption({
                title: {
                        text: '用户等级',
                        // subtext: '纯属虚构',
                        x: 'center'
                    },
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ["注销用户", "vip用户", "扩展用户"]
                    },
                    series: [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: [
                                { value: 335, name: '注销用户' },
                                { value: 310, name: 'vip用户' },
                                { value: 234, name: '扩展用户' } 
                            ],
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
            },
            drawBarChart() {
                this.chartBar = echarts.init(document.getElementById('chartBar'));
                this.chartBar.setOption({
                     title: {
                        text: '商家类型',
                        // subtext: '纯属虚构',
                        x: 'center'
                    },
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ["大商家", "中商家", "小商家"]
                    },
                    series: [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: [
                                { value: 335, name: '大商家' },
                                { value: 310, name: '中商家' },
                                { value: 234, name: '小商家' } 
                            ],
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
            },
            drawLineChart() {
                this.chartLine = echarts.init(document.getElementById('chartLine'));
                this.chartLine.setOption({
                    title: {
                        // text: ' '
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        // data: ['1', '1', '1']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: ['未联系上', '已联系', '初步沟通', '确定合作', '已合作', '悔单', '放弃', '信息错误', '未联系']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        { 
                            type: 'line',
                            stack: '总量',
                            data: [120, 132, 101, 134, 90, 230, 210]
                        },
                        { 
                            type: 'line',
                            stack: '总量',
                            data: [220, 182, 191, 234, 290, 330, 310]
                        },
                        { 
                            type: 'line',
                            stack: '总量',
                            data: [820, 932, 901, 934, 1290, 1330, 1320]
                        }
                    ]
                });
            },
            drawPieChart() {
                this.chartPie = echarts.init(document.getElementById('chartPie'));
                this.chartPie.setOption({
                    title: {
                        text: '使用情况',
                        // subtext: '纯属虚构',
                        x: 'center'
                    },
                    tooltip: {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ["未使用", "使用中", "暂停使用"]
                    },
                    series: [
                        {
                            name: '访问来源',
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: [
                                { value: 335, name: '未使用' },
                                { value: 310, name: '使用中' },
                                { value: 234, name: '暂停使用' } 
                            ],
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
            },
            
            drawLineChart1() {
                this.chartLine1 = echarts.init(document.getElementById('chartLine1'));
                this.chartLine1.setOption({
                    title: {
                        text: '今日包裹数'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['包裹数']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        // data: [ ]
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        // { 
                        //     type: 'line',
                        //     stack: '总量',
                        //     data: [120, 132, 101, 134, 90, 230, 210]
                        // } 
                    ]
                });
            },
              drawLineChart2() {
                this.chartLine2 = echarts.init(document.getElementById('chartLine2'));
                this.chartLine2.setOption({
                    title: {
                        text: '新增注册数'
                    },
                    tooltip: {
                        trigger: 'axis'
                    },
                    legend: {
                        data: ['新增注册']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        // data: [ ]
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [
                        // { 
                        //     type: 'line',
                        //     stack: '总量',
                        //     data: [120, 132, 101, 134, 90, 230, 210]
                        // } 
                    ]
                });
            },
            drawCharts() {
                this.drawColumnChart()
                this.drawBarChart()
                this.drawLineChart()
                this.drawPieChart()
                this.drawLineChart1()
                this.drawLineChart2()
            },
        },

        mounted: function () {
            this.drawCharts()
        },
        updated: function () {
            this.drawCharts()
        }
    }
</script>

<style scoped>
    .chart-container {
        width: 100%;
        float: left;
    }
    /*.chart div {
        height: 400px;
        float: left;
    }*/

    .el-col {
        padding: 30px 20px;
    }
</style>



